/* https://jasonwatmore.com/post/2018/08/07/javascript-pure-pagination-logic-in-vanilla-js-typescript */

const paginate = (
  totalItems: number,
  currentPage = 1,
  pageSize = 10,
  maxPages = 10
) => {
  // calculate total pages
  const totalPages = Math.ceil(totalItems / pageSize)
  // ensure current page isn't out of range
  if (currentPage < 1) {
    currentPage = 1
  } else if (currentPage > totalPages) {
    currentPage = totalPages
  }

  let startPage: number, endPage: number
  if (totalPages <= maxPages) {
    // total pages less than max so show all pages总页数少于最大，因此显示所有页
    startPage = 1
    endPage = totalPages
  } else {
    // total pages more than max so calculate start and end pages总页数超过了最大值，因此请计算开始页和结束页
    const maxPagesBeforeCurrentPage = Math.floor(maxPages / 2)
    const maxPagesAfterCurrentPage = Math.ceil(maxPages / 2) - 1
    if (currentPage <= maxPagesBeforeCurrentPage) {
      // current page near the start当前页面在开始附近
      startPage = 1
      endPage = maxPages
    } else if (currentPage + maxPagesAfterCurrentPage >= totalPages) {
      // current page near the end当前页面即将结束
      startPage = totalPages - maxPages + 1
      endPage = totalPages
    } else {
      // current page somewhere in the middle当前页面在中间某处
      startPage = currentPage - maxPagesBeforeCurrentPage
      endPage = currentPage + maxPagesAfterCurrentPage
    }
  }

  // calculate start and end item indexes计算开始和结束项目索引
  const startIndex = (currentPage - 1) * pageSize
  const endIndex = Math.min(startIndex + (pageSize - 1), totalItems - 1)
  // create an array of pages to ng-repeat in the pager control
  const pages = Array.from(Array((endPage + 1) - startPage).keys()).map(i => startPage + i)

  // return object with all pager properties required by the view返回具有视图所需的所有属性的对象
  return {
    totalItems: totalItems,
    currentPage: currentPage,
    pageSize: pageSize,
    totalPages: totalPages,
    startPage: startPage,
    endPage: endPage,
    startIndex: startIndex,
    endIndex: endIndex,
    pages: pages
  }
}

export default paginate
